.controls {
  position: absolute;
  bottom: -60px;
  left: 50%;
  transform: translateX(-50%);
  width: 280px;
  height: 111px;
}

.controls__top {
  width: 100%;
  height: 45px;
  background: $black;
  opacity: 0.5;
  padding: 22px;
  box-sizing: border-box;
}

.controls__top-scale {
  width: 100%;
  height: 3px;
  background: $white;
  position: relative;
}

.controls__top-slider {
  position: absolute;
  top: 18px;
  left: 60px;
  width: 13px;
  height: 13px;
  background: $white;
  border-radius: 50%;
}

.controls__down {
  width: 100%;
  height: 66px;
  background: $light-grey;
}

@media (min-width: $tablet-width) {
  .controls {
    width: 648px;
    bottom: 80px;
  }
}

@media (min-width: $desktop-width) {
  .controls {
    width: 800px;
  }
}
